import React from "react";
import {render} from "react-dom";
import "../Style/Components/Logo.css"
import logoImage from "../Imgs/logo.png"

class Logo extends React.Component{

    constructor(props){
        super(props);
    }
    render(){
        return(
            <React.Fragment>
                <div className={"Logo"}>
                    <img className="logoImage" src={logoImage}/>
                    <div className="logoText">Your IT   <br/>Department</div>
                </div>
            </React.Fragment>

        )
    }
}


export default Logo